@font-face {
  font-family: "Inter-Regular";
  src: local("Inter-Regular"),
    url(/static/media/Inter-Regular.079af0e2.ttf) format("truetype");
}
@font-face {
  font-family: "Inter-Medium";
  src: local("Inter-Medium"),
    url(/static/media/Inter-Medium.ed533866.ttf) format("truetype");
}

body {
  font-family: "Inter-Regular";
}

h1, h2 {
  font-family: "Inter-Medium";
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  margin-right: 5%;
  margin-left: 5%;
}

html {
  scroll-behavior: smooth;
}

.title {
  text-align: right;
  font-size: 72px;
  font-weight: 500;
  margin: 0%;
  margin-bottom: 1%;
}

.section--header {
  font-size: 56px;
  font-weight: 500;
}

.navbar--menu {
  display: flex;
  justify-content: flex-end;
  grid-gap: 5%;
  gap: 5%;
  font-size: 40px;
  margin-bottom: 10%;
  padding: 10px;
  padding-top: 0px;
  color: black;
}

.navbar--item {
  text-decoration: none;
  color: black;
}

.navbar {
  background-image: url(/static/media/yarn2.9f785cc9.svg);
  background-repeat: no-repeat;
  background-position-y: bottom;
  background-size: 575px;
  margin: 0%;
}



.intro--typewriter{
  background-image: url(/static/media/typewriter2.bf003ce0.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position-y:bottom;
}

.intro--textborder {
  padding-left: 15%;
  padding-right: 13%;
  padding-bottom: 70%;
  /* padding-top: 25px; */
  
}

.intro--text {
  
  font-size: 18px;
  align-content: center;
  border-style: solid;
  border-color: #6A30A5;
  border-width: 5px;
  padding: 5%;

}

.projects--gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.project {
  /* border: 10px solid green; */
  width: 500px;
  height: 359px;
  font-size: 40px;
  padding: 10px;
  text-decoration: none;
  color: black;
}

.project--frame1 {
  background-image: url(/static/media/frame1.d34bcc9b.svg);
  background-repeat: no-repeat;
  background-size: contain;
  
  width: inherit;
  height: inherit;
  padding: 0px 75px;
  
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

.project--frame2 {
  background-image: url(/static/media/frame2.81a0566c.svg);
  background-repeat: no-repeat;
  background-size: contain;

  width: inherit;
  height: inherit;
  padding: 0px 75px;
  
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

.project--frame3 {
  background-image: url(/static/media/frame3.8bf11b1b.svg);
  background-repeat: no-repeat;
  background-size: contain;

  width: inherit;
  height: inherit;
  padding: 0px 75px;
  
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact--button {
  margin: 0;
  border: 0;
  padding: 0px;
  background : none;
}

.contact--buttons {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  grid-gap: 1%;
  gap: 1%;
}

p {
  text-align: left;
}

.pagehead {
  display:flex;
  grid-gap:2%;
  gap:2%;
  
}

.pagehead--logo {
  margin: 0;
  border: 0;
  padding: 0px;
  background : none;
  padding-top: 10px;  
}

.pagehead--title {
  text-align: left;
  align-self: center;
  font-size: 64px;
  font-weight: 500;
  margin: 0%;
}



.magazine--viewer {
  height: 435px;
  width: 391px;
  float: right;
  margin-right: 50px;
  margin-left: 50px;
}

.page--body {
  margin-top: 50px;
}

.page--body > h2 {
  text-align: left;
}

ul {
  text-align: left;
}

figure > img {
  max-width:80%;
}

.rupaul--images {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px
  
}

h2 {
  text-align: "left"
}
